body {
  margin: 0;
}

.header {
  padding: 8px 10px;
  display: flex;
  justify-content: space-between;
}

.header svg {
  cursor: pointer;
}

.card {
  margin: 20% 8px;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60vh;
  position: relative;
  z-index: 10;
  overflow: hidden;
}

.card-1-line {
  justify-content: center;
}

.card-3-lines {
  justify-content: space-between;
}

.progress {
  position: absolute;
  width: calc(100% + 6px);
  z-index: 9;
  top: -11px;
}

.btns {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.word-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.word {
  font-size: 3em;
  text-align: center;
}

.direction {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
}

.direction img {
  width: 30%;
  height: 30%;
  margin: 0 3px;
}

.direction svg {
  margin: 0 3px;
}

.stat {
  display: flex;
  flex-direction: column;
}

.stat-item {
  padding: 10px;
}
